import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, CheckCircle, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import { useHistory } from 'react-router-dom'
import useJwt from '@src/auth/jwt/useJwt'
import { toast } from 'react-toastify'
import { PHONE_PREFIX } from '../../../Configuarables'
import { Error, Success, ErrorMessage } from '../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import CommonDataTable from '../../CommonDataTable'
import EditModal from './EditModal'

const EstateAdminList = () => {
    const [TableDataLoading, setTableDataLoading] = useState(true)
    const [resetData, setReset] = useState(true)
    const [managerList, setmanagerList] = useState([])
    const [ManagerInfo, setManagerInfo] = useState({})

    const [modal, setModal] = useState(false)
    const toggleModal = () => setModal(!modal)

    useEffect(() => {
        useJwt.EstateAdminsList().then(res => {
            console.log(res.data.payload)
            setmanagerList(res.data.payload)
            setTableDataLoading(false)
        }).catch(err => {
            Error(err)
            console.log(err)
            setTableDataLoading(false)
        })
    }, [resetData])

    const handleActivate = estate_manager_id => {
        // activateEstateManager
        return MySwal.fire({
            title: 'Are you sure to activate?',
            // text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                // console.log({ land_lord_id })
                return useJwt.activateEstateManager({ estate_manager_id }).then(res => {
                    console.log(res)
                    setmanagerList(managerList.map(x => {
                        if (x.user_id === estate_manager_id) {
                            return { ...x, status: 1 }
                        } else { return x }
                    }))
                }).catch(err => {
                    Error(err)
                    console.log(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {
            }
        })
    }
    const handleBlock = estate_manager_id => {
        // blockEstateManager
        return MySwal.fire({
            title: 'Are you sure to block?',
            // text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                // console.log({ land_lord_id })
                return useJwt.blockEstateManager({ estate_manager_id }).then(res => {
                    console.log(res)
                    setmanagerList(managerList.map(x => {
                        if (x.user_id === estate_manager_id) {
                            return { ...x, status: 0 }
                        } else { return x }
                    }))
                }).catch(err => {
                    Error(err)
                    console.log(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {
            }
        })
    }
    const handleDelete = estate_manager_id => {
        // blockEstateManager
        return MySwal.fire({
            title: 'Are you sure to delete?',
            text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                // console.log({ land_lord_id })
                return useJwt.deleteEstateAdmin({ estate_manager_id }).then(res => {
                    console.log(res)
                    setmanagerList(managerList.filter(x => x.user_id !== estate_manager_id))
                }).catch(err => {
                    Error(err)
                    console.log(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {
            }
        })
    }
    const column = [
        // {
        //     name: 'ID',
        //     maxWidth: '50px',
        //     sortable: true,
        //     selector: 'user_id'
        // },
        {
            name: 'Name',
            minWidth: '100px',
            sortable: true,
            selector: 'full_name'
        },
        {
            name: 'Email',
            minWidth: '100px',
            sortable: true,
            selector: 'email'
        },
        {
            name: 'Mobile',
            minWidth: '100px',
            sortable: true,
            selector: 'mobile'
        },
        {
            name: 'Address',
            minWidth: '100px',
            // sortable: true,
            selector: 'address'
        },
        {
            name: 'Commission',
            minWidth: '50px',
            // sortable: true,
            selector: 'commission_percentage'
        },
        {
            name: 'Charge',
            minWidth: '50px',
            // sortable: true,
            selector: row => row.tenant_charge.charge_name
        },
        {
            name: 'Status',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                const statusBG = {
                    0: { title: 'Blocked', color: 'light-danger' },
                    1: { title: 'Active', color: 'light-success' }
                }
                return <Badge color={statusBG[row.status].color} pill className='px-1'>
                    {statusBG[row.status].title}
                </Badge>
            }
        },
        {
            name: 'Action',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                return <>
                    <span title="Edit">
                        <Edit size={15}
                            color='teal'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => {
                                setManagerInfo(row)
                                setModal(true)
                            }}
                        />
                    </span>&nbsp;&nbsp;
                    <span title="Delete">
                        <Trash size={15}
                            color='crimson'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handleDelete(row.user_id)}
                        />
                    </span>&nbsp;&nbsp;
                    {row.status ? <span title="Block">
                        <Lock size={15}
                            color='black'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handleBlock(row.user_id)}
                        />
                    </span> : <span title="Activate">
                        <CheckCircle size={15}
                            color='dodgerblue'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handleActivate(row.user_id)}
                        />
                    </span>}
                </>
            }
        }
    ]
    return (
        <Card>
            <CardHeader className='border-bottom'>
                <CardTitle tag='h4'>Estate Admin List</CardTitle>
            </CardHeader>
            <CardBody>
                <Row>
                    <Col md='12'>
                        <CommonDataTable column={column} TableData={managerList} TableDataLoading={TableDataLoading} />
                    </Col>
                </Row>
                <EditModal
                    toggleModal={toggleModal}
                    modal={modal}
                    resetData={resetData}
                    setReset={setReset}
                    setManagerInfo={setManagerInfo}
                    ManagerInfo={ManagerInfo}
                />
            </CardBody>
        </Card>
    )
}

export default EstateAdminList