import { Fragment, useState, useEffect, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical,
    Edit, Archive, Trash, Search, ChevronLeft, Eye, XCircle, Facebook, Globe, Instagram, Twitter
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, CardBody, CustomInput, Table, Spinner, InputGroup, InputGroupAddon, InputGroupText, FormFeedback, Progress, Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import { PHONE_PREFIX } from '../../../Configuarables'
import { Error, Success, ErrorMessage } from '../../../viewhelper'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'

const EditModal = ({ modal, toggleModal, ManagerInfo, setManagerInfo, resetData, setReset }) => {
    const addressRef = useRef()
    const [ukaddress, setukaddress] = useState([])
    const [postcode, setPostCode] = useState('')
    const [searching, setSearching] = useState(false)
    const [TenantChargeList, setTenantChargeList] = useState([])
    useEffect(() => {
        useJwt.TenantChargesList().then(res => {
            console.log(res.data.payload)
            setTenantChargeList(res.data.payload)
        }).catch(err => {
            Error(err)
            console.log(err)
        })
    }, [])
    const handlePostCodeSearch = e => {
        e.preventDefault()
        setSearching(true)
        useJwt.ukAddressEndpoint({ postCode: postcode }).then(res => {
            console.log(res.data.payload)
            // const { latitude, longitude } = res.data.payload
            // setUserInput({ ...ManagerInfo, latitude, longitude })
            setukaddress(res.data.payload.addresses.map(element => `${element.building_number}, ${element.thoroughfare}, ${element.locality}, ${element.town_or_city}`))
            setSearching(false)
        }).catch(err => {
            setukaddress([])
            setSearching(false)
            Error(err)
        })
    }

    const [isLoading, setLoading] = useState(false)

    const [error, seterror] = useState({
        email: false,
        mobile: false
    })
    const handleChange = e => {
        const chkEmail = /\S+@\S+\.\S+/.test(e.target.value)
        // Email Validator
        if (e.target.name === 'email' && chkEmail) {
            seterror({ ...error, email: false })
        }
        if (e.target.name === 'email' && !chkEmail) {
            seterror({ ...error, email: true })
        }
        //mobile number validator
        if (e.target.name === "mobile") {
            // console.log(e.target.value)
            if (e.target.value[0] === '0') {
                seterror({ ...error, mobile: true })
                return 0
            } else {
                seterror({ ...error, mobile: false })
            }
            if (!/^[0-9]*$/.test(e.target.value)) {
                return 0
            }
        }
        setManagerInfo({ ...ManagerInfo, [e.target.name]: e.target.value })
    }
    const handleSubmit = e => {
        e.preventDefault()
        console.log(ManagerInfo)
        const { user_id, mobile, email, full_name, address, tenant_charge, commission_percentage } = ManagerInfo
        const per_tenant_charge_id = tenant_charge.id
        const chkEmail = /\S+@\S+\.\S+/.test(email)
        if (!chkEmail) {
            seterror({ ...error, email: true })
            return 0
        }
        setLoading(true)
        console.log({ estate_manager_id: user_id, mobile, email, full_name, address, per_tenant_charge_id, commission_percentage })
        useJwt.editEstateManager({ estate_manager_id: user_id, mobile, email, full_name, address, per_tenant_charge_id, commission_percentage }).then(res => {
            console.log(res)
            Success(res)
            setLoading(false)
            toggleModal()
            setReset(!resetData)
        }).catch(err => {
            Error(err)
            setLoading(false)
        })
    }
    return (
        <Modal isOpen={modal} toggle={toggleModal} className='modal-dialog-centered'>
            <ModalHeader toggle={toggleModal}>Edit Estate Admin Info</ModalHeader>
            <ModalBody>
                <Form className="row" style={{ width: '100%' }} onSubmit={handleSubmit} autoComplete="off">
                    <Col md='6'>
                        <FormGroup>
                            <Label for="full_name">Full Name</Label>
                            <Input
                                // required
                                type="text"
                                id='full_name'
                                name="full_name"
                                placeholder="manager name"
                                value={ManagerInfo.full_name}
                                onChange={handleChange}
                            />
                        </FormGroup>
                    </Col>
                    <Col md='6' >
                        <FormGroup>
                            <Label className='form-label' for='mobile'>
                                Mobile Number
                            </Label>
                            <InputGroup className='input-group-merge w-100 mb-1'>
                                <InputGroupAddon addonType='prepend'>
                                    <InputGroupText>
                                        {PHONE_PREFIX}
                                    </InputGroupText>
                                </InputGroupAddon>
                                <Input
                                    autoFocus
                                    // required
                                    type='text'
                                    minLength={10}
                                    maxLength={10}
                                    id='mobile'
                                    name='mobile'
                                    value={ManagerInfo.mobile}
                                    onChange={handleChange}
                                    placeholder='123-456-7899'
                                />
                            </InputGroup>
                            {error.mobile && <span style={{ color: 'red', fontSize: '11px' }}>First digit 0 not allow</span>}
                        </FormGroup>
                    </Col>
                    <Col md='12' >
                        <FormGroup>
                            <Label for="email">Email</Label>
                            <Input
                                // required
                                type="email"
                                id='email'
                                name="email"
                                placeholder="example@gmail.com"
                                value={ManagerInfo.email}
                                onChange={handleChange}
                            />
                            {error.email && ManagerInfo.email && <span style={{ color: 'red', fontSize: '11px' }}>Please Enter a valid Email</span>}
                        </FormGroup>
                    </Col>
                    <Col md='12' >
                        <Label for="postcode">Search Address by Post Code</Label>
                        <InputGroup>
                            <Input
                                // required
                                type="text"
                                id='postcode'
                                name="postcode"
                                placeholder="post code"
                                value={postcode}
                                onChange={e => setPostCode(e.target.value)}
                            />
                            <InputGroupAddon addonType='append'>
                                <Button color='primary' outline type='button' onClick={handlePostCodeSearch}>
                                    {searching ? <Spinner size='sm' /> : 'Search'}
                                </Button>
                            </InputGroupAddon>
                        </InputGroup>
                    </Col>
                    <Col md='12' >
                        <Label>Select Address</Label>
                        <Select
                            ref={addressRef}
                            theme={selectThemeColors}
                            className='react-select'
                            classNamePrefix='select'
                            name='address'
                            onChange={selected => {
                                setManagerInfo({ ...ManagerInfo, address: selected.value })
                            }}
                            options={ukaddress.map(x => { return { value: x, label: x } })}
                            value={{ value: ManagerInfo.address, label: ManagerInfo.address }}
                            // isDisabled={!ukaddress.length}
                            maxMenuHeight={150}
                            isClearable={false}
                        />
                    </Col>
                    {/* <Col md='12' >
                        <Label>address</Label>
                        <Input
                            // required
                            type="text"
                            id='address'
                            name="address"
                            placeholder="address"
                            value={ManagerInfo.address}
                            onChange={handleChange}
                        />
                    </Col> */}
                    <Col md='6' className='my-1'>
                        <FormGroup>
                            <Label for="commission_percentage">commission_percentage (%)</Label>
                            <Input
                                // required
                                type="number"
                                min={0}
                                max={99}
                                id='commission_percentage'
                                name="commission_percentage"
                                placeholder="0"
                                value={ManagerInfo.commission_percentage}
                                onChange={handleChange}
                            />
                        </FormGroup>
                    </Col>
                    <Col md='6' className='my-1'>
                        <FormGroup>
                            <Label for="per_tenant_charge">Charge Per Tenant </Label>
                            {ManagerInfo.tenant_charge && <Select
                                theme={selectThemeColors}
                                className='react-select'
                                classNamePrefix='select'
                                name='address'
                                onChange={selected => {
                                    setManagerInfo({ ...ManagerInfo, tenant_charge: { ...ManagerInfo.tenant_charge, id: selected.value } })
                                }}
                                maxMenuHeight={100}
                                options={TenantChargeList.map(x => { return { value: x.id, label: x.charge_name } })}
                                value={TenantChargeList.filter(charge => charge.id === ManagerInfo.tenant_charge.id).map(x => { return { value: x.id, label: x.charge_name } })}
                                // isDisabled={!ukaddress.length}
                                isClearable={false}
                            />}
                            {/* <Input
                                required
                                type="number"
                                min={0}
                                id='per_tenant_charge'
                                name="per_tenant_charge"
                                placeholder="amount"
                                value={ManagerInfo.tenant_charge.charge_amount}
                                onChange={handleChange}
                            /> */}
                        </FormGroup>
                    </Col>
                    <Col md="12 text-center">
                        <Button.Ripple className='mt-2' color='primary' type="submit" disabled={isLoading}>
                            {isLoading ? <Spinner size='sm' /> : 'Update'}
                        </Button.Ripple>
                    </Col>
                </Form>
            </ModalBody>
        </Modal>
    )
}

export default EditModal